(function() {
  'use strict';

  angular
  .module('angular-gapi')
  .service('GAuth', GAuth);

  /** @ngInject */
  function GAuth(GApi, $q) {
    var config;
    var cachedAuth2 = null;
    var initialized = false;

    var service = {
      setConfig: setConfig,
      checkAuth: checkAuth,
      signIn: signIn,
      signOut: signOut
    };
    return service;

    // Takes an object like this:
    // {
    //   clientId: 'xyz.apps.googleusercontent.com',
    //   scope: 'https://www.googleapis.com/auth/calendar',
    //   currentUserListener: function(currentUser) { ... }
    // }
    function setConfig(conf) {
      config = conf;
    }

    function signIn() {
      init().then(function(auth2) {
        auth2.getAuthInstance().signIn();
      });
    }

    function signOut() {
      init().then(function(auth2) {
        auth2.getAuthInstance().signOut();
      });
    }

    function checkAuth() {
      // init() checks the authentiation but by just calling it (but not
      // returning it) we avoid exposing promise that would return the auth2 instance.
      init();
    }

    function init() {
      if (initialized) {
        return get();
      } else {
        return get().then(function(auth2) {
          auth2.init({ client_id: config.clientId, scope: config.scope });
          auth2.getAuthInstance().currentUser.listen(config.currentUserListener);
          initialized = true;
          return auth2;
        });
      }
    }

    function get() {
      if (cachedAuth2) {
        return $q.resolve(cachedAuth2);
      } else {
        return GApi.get().then(function(gapi) {
          cachedAuth2 = gapi.auth2;
          return gapi.auth2;
        });
      }
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('angular-gapi')
  .service('GApi', GApi);

  /** @ngInject */
  function GApi($q, $document, $window, $timeout) {
    var gapiLoaded = false;
    var gapiLoading = false;
    var observerCallbacks = [];

    this.get = get;
    this.request = request;

    function request(args) {
      return get().then(function(gapi) {
        return gapi.client.request(args);
      });
    }

    function get() {
      if (gapiLoaded) {
        return $q.when($window.gapi);
      } else {
        var deferred = $q.defer();
        observerCallbacks.push(deferred);
        if (!gapiLoading) {
          gapiLoading = true;
          loadWithClientAndAuth().then(function(gapi) {
            gapiLoaded = true;
            gapiLoading = false;
            observerCallbacks.forEach(function(c) { c.resolve(gapi); });
          });
        }
        return deferred.promise;
      }
    }

    function loadWithClientAndAuth() {
      var deferred = $q.defer();
      loadScript().then(function(gapi) {
        gapi.load('client:auth2', function() {
          deferred.resolve(gapi);
        });
      });
      return deferred.promise;
    }

    function loadScript() {
      var URL = 'https://apis.google.com/js/api.js?onload=_gapiOnLoad';

      var deferred = $q.defer();
      $window._gapiOnLoad = function(){
        deferred.resolve($window.gapi);
      }
      var script = $document[0].createElement('script');
      script.onerror = function (e) {
        $timeout(function () {
          deferred.reject(e);
        });
      };
      script.src = URL;
      $document[0].body.appendChild(script);
      return deferred.promise;
    }
  }
})();

